import { generatePdf, sendEmail } from '../../Api/api';
import { ReducerTypes } from './types';
// Later from the frontend component / redux when typed
import { NotificationType } from '../../globalTypes';
var pending = function (id, title) { return ({
    variant: NotificationType.info,
    title: title
        ? title
        : 'Your report is being generated and will download shortly.',
    id: id,
    autoDismiss: false,
}); };
var rejected = function (id, title, message) { return ({
    variant: NotificationType.danger,
    title: title
        ? title
        : "There was an error generating your report. Please try again.",
    description: message ? "Details: " + message : '',
    autoDismiss: false,
    id: id,
}); };
var success = function (id, title) { return ({
    variant: NotificationType.success,
    title: title ? title : 'Email sent successfully.',
    id: id,
}); };
export var downloadPdf = function (params, dispatch, slug, token) { return ({
    type: ReducerTypes.act,
    payload: generatePdf(params, {
        pending: pending,
        rejected: rejected,
        success: success,
        dispatch: dispatch,
        id: slug,
    }),
    meta: { slug: slug, token: token },
}); };
export var email = function (params, dispatch, slug, token) { return ({
    type: ReducerTypes.act,
    payload: sendEmail(params, {
        pending: pending,
        rejected: rejected,
        success: success,
        dispatch: dispatch,
        id: slug,
    }),
    meta: { slug: slug, token: token },
}); };
